    width: 326px;
    padding-right: 14px;
    padding-top: 5px;
    padding-left: 5px;
    padding-bottom: 5px;
  }
  .swiper-scrollbar-drag {
    background: #04a3e9;
    border-radius: 0;
  }
  .swiper-scrollbar.swiper-scrollbar-vertical,
  .swiper-vertical > .swiper-scrollbar {
    width: 4px;
    background: #f3f3f6;
  }
  .mainswiperpreview .swiper-slide {
    height: 156px;
    opacity: 0.5;
    border-radius: 10px;
    border: 2px solid #fff;
    cursor: pointer;
  }
  .mainswiperpreview .swiper-slide-thumb-active {
    opacity: 1;
    outline: 3px solid #0084fe !important;
  }
  .swiper-slide a {
    display: block;
    width: 100%;
    height: 100%;
  }
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    border-radius: 10px;
